import Vue from 'vue'

const layout = {
  template:'<transition name="el-zoom-in-top">'+
    '<div @click="close()" v-show="visible" style="width:100%;z-index:2002;height:100%;position:fixed;top:0;left:0;background:rgba(0,0,0,.3)">X</div>'+
    '</transition>',
  data(){
    return {
      visible: true
    }
  },
  methods: {
    close(){
      this.visible = !this.visible
    }
  }
}

let layoutConstructor = Vue.extend(layout);
let instance = null;

function Layout(options){
  instance = new layoutConstructor();
  instance.vm = instance.$mount();
  document.body.appendChild(instance.vm.$el);
  instance.vm.visible = true;
  instance.dom = instance.vm.$el;
  return instance.vm;
}

Layout.close = function(){
  instance.vm.visible = false
}

export default  Layout


